.pure-table {
    /* Remove spacing between table cells (from Normalize.css) */
    border-collapse: collapse;
    border-spacing: 0;
    empty-cells: show;
    border: 1px solid #dedede;
    width: 100%;
}

.pure-table caption {
    color: #000;
    font: italic 85%/1 arial, sans-serif;
    padding: 1em 0;
    text-align: center;
}

.pure-table td,
.pure-table th {
    border-left: 1px solid #dedede; /*  inner column border */
    border-width: 0 0 1px 1px;
    font-size: inherit;
    margin: 0;
    overflow: visible; /*to make ths where the title is really long work*/
    padding: 0.7em; /* cell padding */
}

/* Consider removing this next declaration block, as it causes problems when
there's a rowspan on the first cell. Case added to the tests. issue#432 */
.pure-table td:first-child,
.pure-table th:first-child {
    border-left-width: 0;
}

.pure-table thead {
    background-color: #5659c0;
    color: #fff;
    text-align: left;
    vertical-align: bottom;
}

/*
striping:
   even - #fff (white)
   odd  - #f2f2f2 (light gray)
*/
.pure-table td {
    background-color: transparent;
}

.pure-table-odd td {
    background-color: #f1f3f8;
}

/* nth-child selector for modern browsers */
.pure-table-striped tr:nth-child(2n) td {
    background-color: #f1f3f8;
}

/* BORDERED TABLES */
.pure-table-bordered td {
    border-bottom: 1px solid #dedede;
}

.pure-table-bordered tbody > tr:last-child > td {
    border-bottom-width: 0;
}

/* HORIZONTAL BORDERED TABLES */

.pure-table-horizontal td,
.pure-table-horizontal th {
    border-width: 0 0 1px 0;
    border-bottom: 1px solid #dedede;
}

.pure-table-horizontal tbody > tr:last-child > td {
    border-bottom-width: 0;
}

.pure-table-none,
.pure-table-none td,
.pure-table-none th {
    border: 0;
}

.pure-table-hover tr:hover td {
    background-color: #fff2f2;
}

.pure-table-line {
    border: 0;
}

.pure-table-line td,
.pure-table-line th {
    border-width: 0 0 1px 0;
    border-bottom: 1px solid #fff;
}

.pure-table-line tbody > tr:last-child > td {
    border-bottom: 0;
}

